.container {
    max-width: 90vw;
    width: 400px;
    margin: 0 auto;
    & .title {
        text-align: center;
        font-size: 24px;
    }
}

.dropdown {
	display: block;
	width: 150px;
    height: 30px;
	padding: 3px;
	margin: 0 20px;
	border-radius: 5px;
	outline: none;
}

.flex {
    margin-top: 20px;
    display: flex;
    & p {
        margin: 0;
    }
}

.button {

}

.card-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.wrap p {
    margin: 10px 0 0;
}

.card {
    width: 150px;
    height: 150px;
    border: 2px solid #000;
    margin-top: 20px;

    & img {
        width: 100%;
    }
}